import cn from 'classnames';

import styles from './ConfirmationStage.module.scss';

const ConfirmationStage = ({setConfirmation}) => {
  return (
    <div className={styles.confirmationContainer}>
      <div className={styles.confirmation}>
        <p className={styles.confirmationTitle}>Подтвердить заказ</p>
        <div className={styles.buttonsContainer}>
          <button className={cn(styles.confirmationButton, styles.confirmButton)}>Подвердить</button>
          <button className={cn(styles.confirmationButton, styles.returnButton)} onClick={() => setConfirmation(false)}>Вернуться</button>
        </div>
      </div>
    </div>
  )
};

export default ConfirmationStage;